@use 'base/mixins' as v;
@use 'base/mixins' as m;

.devwebcamp{

    &__grid{
        @include m.contenedor;
        @include m.grid(1, 3rem);
        @include m.tablet{
            @include m.grid(2, 5rem);
            align-items: center;
        }
    }

    &__texto{
        line-height: 2;
    }

}